import * as React from "react";
import { Stack, Switch, Typography } from "@mui/material";
import { useDataFormat } from "./DataFormatSelectors";
import { DATA_FORMAT_SLICE, DistanceFormat } from "./DataFormatSlice";
import { useDispatch } from "react-redux";

export function DistanceFormatSelector(): JSX.Element {
  const { distanceFormat } = useDataFormat();
  const dispatch = useDispatch();

  const handleOnChange = (e: React.ChangeEvent<HTMLInputElement>, checked: boolean) => {
    const newDistanceFormat = checked
      ? DistanceFormat.METERS
      : DistanceFormat.YARDS;

    dispatch(DATA_FORMAT_SLICE.actions.setDistanceFormat(newDistanceFormat));
  };

  return (
    <Stack direction="row" spacing={1} alignItems="center">
      <Typography>Yards</Typography>
      <Switch
        checked={distanceFormat === DistanceFormat.METERS}
        onChange={handleOnChange}
      />
      <Typography>Meters</Typography>
    </Stack>
  );
}
