import { DistanceFormat } from "./DataFormatSlice";

const YARDS_PER_METER = 1.09361;

export function convertToDistanceFormat(
  format: DistanceFormat,
  value: number | undefined
): number | undefined {
    if(value === undefined){
        return undefined;
    }

  switch (format) {
    case DistanceFormat.YARDS:
      return value * YARDS_PER_METER;
    case DistanceFormat.METERS:
    default:
      return value;
  }
}


export function convertFromDistanceFormat(
    format: DistanceFormat,
    value: number | undefined
  ): number | undefined {
    if(value === undefined){
      return undefined;
    }

    switch (format) {
      case DistanceFormat.YARDS:
        return value / YARDS_PER_METER;
      case DistanceFormat.METERS:
      default:
        return value;
    }
  }
  