import { createSelector } from "@reduxjs/toolkit";
import { useSelector } from "react-redux";
import { RootState } from "../../app/ReduxProvider";
import { InputData } from "../../features/input-data/InputDataSlice";
import { isDataComplete } from "../utilities/isDataComplete";
import { CurveFit } from "../../features/curve-fit/CurveFit";
import { CurveFitOptimizer } from "../utilities/CurveFitOptimizer";
import { SplitsData } from "../../features/splits/SplitsSlice";

export interface SprintData {
  time: number;
  position: number;
  velocity: number;
  hztAccel: number;
  airFriction: number;
  hztNetForce: number;
  hztNetForcePerWeight: number;
  hztNetPower: number;
  hztNetPowerPerWeight: number;
  vtcForce: number | undefined;
  ratioOfForce: number | undefined;
}

const INTERVAL_INCREMENT = 0.01;

function getInputData(state: RootState): Partial<InputData> {
  return state.inputData;
}

function getSplitsData(state: RootState): SplitsData {
  return state.splits;
}

export const selectFitVariables = createSelector(
  getInputData,
  getSplitsData,
  (inputData: Partial<InputData>, splits: SplitsData): CurveFit => {
    if (!isDataComplete(inputData)) {
      return new CurveFit(0, 0);
    }

    const initialPop = CurveFitOptimizer.createInitialPopulation();

    const result = new CurveFitOptimizer(splits).findOptimal(initialPop);

    return result;
  }
);

const selectVelocityData = createSelector(
  getInputData,
  selectFitVariables,
  (inputData: Partial<InputData>, fitVariables: CurveFit): SprintData[] => {
    if (!isDataComplete(inputData)) {
      return [];
    }

    const { vmax, tau } = fitVariables;

    let position = 0;
    const result = [];

    for (
      let interval = INTERVAL_INCREMENT;
      interval <= 6;
      interval += INTERVAL_INCREMENT
    ) {
      const velocity = vmax * (1 - Math.exp(-interval / tau));

      position += velocity * INTERVAL_INCREMENT;

      const hztAccel = (vmax / tau) * Math.exp((-1 * interval) / tau);

      const airFriction =
        0.5 *
        (1.293 *
          (((inputData.pressure / 760) * 273) /
            (273 + inputData.temperature))) *
        (0.2025 *
          Math.pow(inputData.stature, 0.725) *
          Math.pow(inputData.mass, 0.425) *
          0.266) *
        0.9 *
        Math.pow(velocity, 2);

      const hztNetForce = airFriction + inputData.mass * hztAccel;

      const hztNetPower = hztNetForce * velocity;

      let vtcForce;
      let ratioOfForce;

      if (interval >= 0.5) {
        vtcForce = inputData.mass * 9.81;
        ratioOfForce =
          hztNetForce /
          Math.sqrt(Math.pow(hztNetForce, 2) + Math.pow(vtcForce, 2));
      }

      result.push({
        position: position,
        velocity: velocity,
        time: interval,
        hztAccel: hztAccel,
        airFriction: airFriction,
        hztNetForce: hztNetForce,
        hztNetForcePerWeight: hztNetForce / inputData.mass,
        hztNetPower: hztNetPower,
        hztNetPowerPerWeight: hztNetPower / inputData.mass,
        ratioOfForce: ratioOfForce,
        vtcForce: vtcForce,
      });
    }

    return result;
  }
);

export function useInputData(): Partial<InputData> {
  return useSelector(getInputData);
}

export function useFitData(): CurveFit {
  return useSelector(selectFitVariables);
}

export function useFullDataSet(): SprintData[] {
  return useSelector(selectVelocityData);
}

export function useSplits(): SplitsData {
  return useSelector(getSplitsData);
}
