import * as React from "react";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
} from "@mui/material";
import { useFitData, useSplits } from "../../common/selectors/DataSelectors";

export function CurveFitValues(): JSX.Element | null {
  const splits = useSplits();
  const curveFit = useFitData();

  const squareDiffs = curveFit.getSquareDiffs(splits);
  const sum = curveFit.getSum(splits);

  return (
    <React.Fragment>
      Vmax: {curveFit.vmax}
      <br />
      Tau: {curveFit.tau}
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>Position model (s)</TableCell>
            <TableCell>Square Differences</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {squareDiffs.map(({ positionModel, sqrDiff }) => {
            return (
              <TableRow key={positionModel}>
                <TableCell>{positionModel.toFixed(1)}</TableCell>
                <TableCell>{sqrDiff.toFixed(1)}</TableCell>
              </TableRow>
            );
          })}

          <TableRow>
            <TableCell />
            <TableCell>
              Sum
              <br />
              {sum.toFixed(10)}
            </TableCell>
          </TableRow>
        </TableBody>
      </Table>
    </React.Fragment>
  );
}
