import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface InputData {
  stature: number;
  mass: number;
  temperature: number;
  pressure: number;
}

const INITIAL_STATE = Object.freeze<Partial<InputData>>({
  // stature: 1.224,
  // mass: 85,
  // temperature: 20,
  // pressure: 900,
});

export const INPUT_DATA_SLICE = createSlice({
  name: "inputData",
  initialState: INITIAL_STATE,
  reducers: {
    setInputData: (
      state: Partial<InputData>,
      action: PayloadAction<Partial<InputData>>
    ) => {
      Object.assign(state, action.payload);
    },
  },
});
