import { Delete } from "@mui/icons-material";
import {
  Button,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  TextField,
} from "@mui/material";
import * as React from "react";
import { useDispatch } from "react-redux";
import { useSplits } from "../../common/selectors/DataSelectors";
import { isFloat } from "../../common/utilities/isInteger";
import { DataFormatInput } from "../data-format/DataFormatInput";
import { useDataFormat } from "../data-format/DataFormatSelectors";
import { DistanceFormat } from "../data-format/DataFormatSlice";
import { SPLITS_DATA_SLICE } from "./SplitsSlice";
import { DistanceFormatLabel } from "../data-format/DistanceFormatLabel";
import { DistanceFormatSelector } from "../data-format/DistanceFormatSelector";
import { useNavigateTo } from "../../common/hooks/useNavigateTo";
import { RoutePaths } from "../../common/Routes";
import { NumericInput } from "../../common/components/NumericInput";

export function Splits(): JSX.Element {
  const splits = useSplits();
  const dispatch = useDispatch();
  const navigate = useNavigateTo();
  const { distanceFormat } = useDataFormat();

  const [splitValue, setSplitValue] = React.useState<number | undefined>();

  const addSplit = () => {
    const splitStr = splitValue?.toString();

    if (!isFloat(splitStr)) {
      return;
    }

    const numericSplit = parseFloat(splitStr);

    dispatch(
      SPLITS_DATA_SLICE.actions.setSplitData({
        split: numericSplit,
        value: undefined,
      })
    );

    setSplitValue(undefined);
  };

  const updateSplitData = (split: number, value: number | undefined) => {
    dispatch(
      SPLITS_DATA_SLICE.actions.setSplitData({
        split: split,
        value: value,
      })
    );
  };

  const advanceToStats = () => {
    navigate(RoutePaths.STATS);
  };

  return (
    <React.Fragment>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>
              Distance ({distanceFormat === DistanceFormat.METERS ? "m" : "y"})
            </TableCell>
            <TableCell>Time (s)</TableCell>
            <TableCell />
          </TableRow>
          <TableRow>
            <TableCell colSpan={3}>
              <DistanceFormatSelector />
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {Object.entries(splits)
            .sort(([a], [b]) => parseInt(a, 10) - parseInt(b, 10))
            .map(([key, value]) => {
              const splitNum = parseFloat(key.toString());

              return (
                <TableRow key={key}>
                  <TableCell>
                    <DistanceFormatLabel value={parseFloat(key)} />
                  </TableCell>
                  <TableCell>
                    <NumericInput
                      value={value}
                      label="Time (s)"
                      onChange={(e) =>
                        updateSplitData(splitNum, e)
                      }
                    />
                  </TableCell>
                  <TableCell>
                    <Button
                      startIcon={<Delete />}
                      onClick={() =>
                        dispatch(SPLITS_DATA_SLICE.actions.removeSplit(splitNum))
                      }
                    />
                  </TableCell>
                </TableRow>
              );
            })}
          <TableRow>
            <TableCell>Add Split</TableCell>
            <TableCell>
              <DataFormatInput
                label="Distance"
                value={splitValue}
                onChange={(newValue) => setSplitValue(newValue)}
              />
            </TableCell>
            <TableCell>
              <Button
                disabled={!isFloat(splitValue?.toString())}
                onClick={() => addSplit()}
              >
                Add
              </Button>
            </TableCell>
          </TableRow>
        </TableBody>
      </Table>
      <Button onClick={advanceToStats}>Next</Button>
    </React.Fragment>
  );
}
