import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export enum DistanceFormat {
  METERS = "meters",
  YARDS = "yards",
}

export interface DataFormaShape {
  distanceFormat: DistanceFormat;
}

const INITIAL_STATE: DataFormaShape = {
  distanceFormat: DistanceFormat.METERS,
};

export const DATA_FORMAT_SLICE = createSlice({
  name: "dataFormat",
  initialState: INITIAL_STATE,
  reducers: {
    setDistanceFormat: (state, action: PayloadAction<DistanceFormat>) => {
      state.distanceFormat = action.payload;
    },
  },
});
