import {
  AppBar,
  Box,
  Button,
  Divider,
  Drawer,
  IconButton,
  List,
  ListItem,
  ListItemButton,
  ListItemText,
  Toolbar,
  Typography,
} from "@mui/material";
import { Menu as MenuIcon } from "@mui/icons-material";
import React from "react";
import { useLocation } from "react-router-dom";
import { RoutePaths } from "../common/Routes";
import { useNavigateTo } from "../common/hooks/useNavigateTo";

interface NavItem {
  url: RoutePaths;
  text: string;
}

export interface HeaderProps {
  title: string;
}

const NAV_ITEMS = Object.freeze<NavItem[]>([
  { text: "input", url: RoutePaths.INPUT },
  { text: "splits", url: RoutePaths.SPLITS },
  { text: "stats", url: RoutePaths.STATS },
  { text: "Curve Fit", url: RoutePaths.CURVE_FIT },
  { text: "Data", url: RoutePaths.DATA_SET },
]);

export function Header(props: HeaderProps): JSX.Element {
  const { title } = props;
  const navigate = useNavigateTo();
  const loc = useLocation();
  const [mobileOpen, setMobileOpen] = React.useState(false);

  const handleDrawerToggle = () => {
    setMobileOpen((prevState) => !prevState);
  };

  const container =
    window !== undefined ? () => window.document.body : undefined;

  return (
    <React.Fragment>
      <AppBar component="nav">
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            sx={{ mr: 2, display: { sm: "none" } }}
          >
            <MenuIcon />
          </IconButton>
          <Typography
            variant="h6"
            component="div"
            sx={{ flexGrow: 1, display: { xs: "none", sm: "block" } }}
          >
            <Button onClick={() => navigate(RoutePaths.HOME)} sx={{ color: "#fff" }}>
              {title}
            </Button>
          </Typography>

          <Box sx={{ display: { xs: "none", sm: "block" } }}>
            {NAV_ITEMS.map(({ text, url }) => (
              <Button
                key={url}
                onClick={() => navigate(url)}
                sx={{ color: loc.pathname === url ? "#ccc" : "#fff" }}
              >
                {text}
              </Button>
            ))}
          </Box>
        </Toolbar>
      </AppBar>
      <Box component="nav">
        <Drawer
          container={container}
          variant="temporary"
          open={mobileOpen}
          onClose={handleDrawerToggle}
          ModalProps={{
            keepMounted: true, // Better open performance on mobile.
          }}
          sx={{
            display: { xs: "block", sm: "none" },
            "& .MuiDrawer-paper": { boxSizing: "border-box", width: 240 },
          }}
        >
          <Box onClick={handleDrawerToggle} sx={{ textAlign: "center" }}>
            <Typography variant="h6" sx={{ my: 2 }}>
              HEADER
            </Typography>
            <Divider />
            <List>
              {NAV_ITEMS.map(({ url, text }) => (
                <ListItem key={url} disablePadding>
                  <ListItemButton
                    sx={{ textAlign: "center" }}
                    onClick={() => navigate(url)}
                  >
                    <ListItemText primary={text} />
                  </ListItemButton>
                </ListItem>
              ))}
            </List>
          </Box>
        </Drawer>
      </Box>
    </React.Fragment>
  );
}
