import * as React from "react";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
} from "@mui/material";
import {
  useSplits,
  useFullDataSet,
  SprintData,
} from "../../common/selectors/DataSelectors";
import { formatAsPercentage } from "../../common/utilities/formatAsPercentage";

interface SlopeAndIntercept {
  slope: number;
  intercept: number;
}

function calculateLinearRegression(
  veloData: SprintData[],
  yKey: keyof SprintData,
  xKey: keyof SprintData
): SlopeAndIntercept {
  const dataArray = [];

  for (const data of veloData) {
    const xVal = data[xKey];
    const yVal = data[yKey];

    if (xVal === undefined || yVal === undefined) {
      continue;
    }

    dataArray.push({
      x: xVal,
      y: yVal,
    });
  }

  // Calculate Sums
  let xSum = 0;
  let ySum = 0;
  let xxSum = 0;
  let xySum = 0;
  let count = dataArray.length;
  for (const data of dataArray) {
    xSum += data.x;
    ySum += data.y;
    xxSum += data.x * data.x;
    xySum += data.x * data.y;
  }

  // Calculate slope and intercept
  var slope = (count * xySum - xSum * ySum) / (count * xxSum - xSum * xSum);
  var intercept = ySum / count - (slope * xSum) / count;

  return {
    slope: slope,
    intercept: intercept,
  };
}

function findMaxSpeed(
  data: SprintData[],
  distance: number
): number | undefined {
  const sortedData = data.slice().sort((a, b) => a.position - b.position);

  for (const vData of sortedData) {
    if (vData.position > distance) {
      return vData.velocity;
    }
  }

  return undefined;
}

function isDefined<T>(val: T | undefined): val is T {
  return val !== undefined;
}

export function Stats(): JSX.Element {
  const velocityData = useFullDataSet();
  const splits = useSplits();

  const { slope: fvSlope, intercept: fZero } = React.useMemo(
    () =>
      calculateLinearRegression(
        velocityData,
        "hztNetForcePerWeight",
        "velocity"
      ),
    [velocityData]
  );

  const { slope: drf } = React.useMemo(
    () => calculateLinearRegression(velocityData, "ratioOfForce", "velocity"),
    [velocityData]
  );

  if(velocityData.length === 0){
    return <span>Please complete the input and splits sections.</span>
  }

  const splitDistances = Object.keys(splits).map((s) => parseInt(s, 10));
  const maxDistance = Math.max(...splitDistances);

  const rofs = velocityData.map((d) => d.ratioOfForce).filter(isDefined);

  const rfMax = Math.max(...rofs);
  const maxSpeed = findMaxSpeed(velocityData, maxDistance);

  const vZero = -fZero / fvSlope;

  return (
    <Table>
      <TableHead>
        <TableRow>
          <TableCell>F0 (N/kg)</TableCell>
          <TableCell>V0 (m/s)</TableCell>
          <TableCell>Pmax (W/kg)</TableCell>
          <TableCell>FV Slope</TableCell>
        </TableRow>
      </TableHead>

      <TableBody>
        <TableRow>
          <TableCell>{fZero.toFixed(2)}</TableCell>
          <TableCell>{vZero.toFixed(2)}</TableCell>
          <TableCell>{((fZero * vZero) / 4).toFixed(2)}</TableCell>
          <TableCell>{fvSlope.toFixed(1)}</TableCell>
        </TableRow>
      </TableBody>
      <TableHead>
        <TableRow>
          <TableCell>RF max (%)</TableCell>
          <TableCell>Drf (%)</TableCell>
          <TableCell>Vopt (m/s)</TableCell>
          <TableCell>Max Speed (m/s)</TableCell>
        </TableRow>
      </TableHead>
      <TableBody>
        <TableRow>
          <TableCell>{formatAsPercentage(rfMax, 0)}</TableCell>
          <TableCell>{formatAsPercentage(drf)}</TableCell>
          <TableCell>{(vZero / 2).toFixed(2)}</TableCell>
          <TableCell>{maxSpeed?.toFixed(2)}</TableCell>
        </TableRow>
      </TableBody>
    </Table>
  );
}
