import { TextField } from "@mui/material";
import * as React from "react";
import { isFloat } from "../utilities/isInteger";

export interface NumericInputProps {
  value: number | undefined;
  label: string;
  onChange(newValue: number | undefined): void;
}

export function NumericInput(props: NumericInputProps): JSX.Element {
  const { value, onChange, label } = props;
  const [inputValue, setInputValue] = React.useState("");

  // Update the state value when the external value changes
  React.useEffect(() => {
    setInputValue(value === undefined ? "" : value.toString());
  }, [value]);

  const handleOnChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setInputValue(e.target.value);

    if(e.target.value === ''){
        onChange(undefined);
        return;
    }

    if (isFloat(e.target.value)) {
      const numericVal = parseFloat(e.target.value);

      if (!isNaN(numericVal)) {
        onChange(numericVal);
      }
    }
  };

  const hasError = inputValue !== "" && !isFloat(inputValue);
  const helperText = hasError ? "Please enter a numeric value" : undefined;

  return (
    <TextField
      error={hasError}
      helperText={helperText}
      label={label}
      value={inputValue}
      onChange={handleOnChange}
    />
  );
}
