const INTEGER_REGEX = new RegExp("^[0-9]*$");
const FLOAT_REGEX = new RegExp("^[+-]?([0-9]*[.])?[0-9]+$");

export function isInteger(value: string | undefined): value is string {
  return value !== undefined && INTEGER_REGEX.exec(value.toString()) !== null;
}

export function isFloat(value: string | undefined): value is string {
  return value !== undefined && FLOAT_REGEX.exec(value) !== null;
}
