import memoizeOne from "memoize-one";
import { SplitsData } from "../splits/SplitsSlice";

export interface SquareDifferenceValue {
  positionModel: number;
  squareDifference: number;
}

export class CurveFit {
  public readonly vmax: number;
  public readonly tau: number;

  constructor(vmax: number, tau: number) {
    this.vmax = vmax;
    this.tau = tau;
  }

  public getSquareDiffs(splits: SplitsData) {
    return this.runComputation(splits, this.vmax, this.tau).squareDiffs;
  }

  public getSum(splits: SplitsData): number {
    return this.runComputation(splits, this.vmax, this.tau).sum;
  }

  private runComputation = memoizeOne(
    (splits: SplitsData, vmax: number, tau: number) => {
      const result = [];
      let sum = 0;

      for (const [strDist, time] of Object.entries(splits)) {
        const distance = parseInt(strDist, 10);

        if (time === undefined) {
          return {
            squareDiffs: [],
            sum: 0,
          };
        }

        const positionModel =
          vmax * (time + tau * Math.exp(-time / tau)) - vmax * tau;
        const sqrDiff = Math.pow(positionModel - distance, 2);

        result.push({
          positionModel: positionModel,
          sqrDiff: sqrDiff,
        });

        sum += sqrDiff;
      }

      return {
        squareDiffs: result,
        sum: sum,
      };
    }
  );
}
