import { Button, FormGroup, TextField } from "@mui/material";
import { Box } from "@mui/system";
import React from "react";
import { useDispatch } from "react-redux";
import { NumericInput } from "../../common/components/NumericInput";
import { useNavigateTo } from "../../common/hooks/useNavigateTo";
import { RoutePaths } from "../../common/Routes";
import { useInputData } from "../../common/selectors/DataSelectors";
import { INPUT_DATA_SLICE } from "./InputDataSlice";

export function InputFields(): JSX.Element {
  const inputData = useInputData();
  const dispatch = useDispatch();
  const navigate = useNavigateTo();

  const advanceScreen = () => {
    navigate(RoutePaths.SPLITS);
  };

  return (
    <React.Fragment>
      <Box component="form" sx={{ "& .MuiTextField-root": { m: 1 } }}>
        <FormGroup>
          <NumericInput
            label="Stature (m)"
            value={inputData.stature}
            onChange={(e) =>
              dispatch(
                INPUT_DATA_SLICE.actions.setInputData({
                  stature: e,
                })
              )
            }
          />

          <NumericInput
            label="Mass (kg)"
            value={inputData.mass}
            onChange={(e) =>
              dispatch(
                INPUT_DATA_SLICE.actions.setInputData({
                  mass: e,
                })
              )
            }
          />

          <NumericInput
            label="T (C)"
            value={inputData.temperature}
            onChange={(e) =>
              dispatch(
                INPUT_DATA_SLICE.actions.setInputData({
                  temperature: e,
                })
              )
            }
          />

          <NumericInput
            label="P (hPa)"
            value={inputData.pressure}
            onChange={(e) =>
              dispatch(
                INPUT_DATA_SLICE.actions.setInputData({
                  pressure: e,
                })
              )
            }
          />
        </FormGroup>
      </Box>
      <Button onClick={advanceScreen}>Next</Button>
    </React.Fragment>
  );
}
