import { CurveFit } from "../../features/curve-fit/CurveFit";
import { SplitsData } from "../../features/splits/SplitsSlice";
import { Optimizer } from "./Optimizer";

const OPTIMIZED_SCORE = 0.0238315;
const INITIAL_POPULATION_SIZE = 1000;
const MAX_GENERATIONS = 1000;

export class CurveFitOptimizer extends Optimizer<CurveFit> {
  public readonly splits: SplitsData;

  public static createInitialPopulation(): readonly CurveFit[] {
    const result = [
      // new CurveFit(7.524290656, 1.21110564716446),
    ];

    for (let i = 0; i < INITIAL_POPULATION_SIZE; i++) {
      result.push(
        new CurveFit(
          7.5 + (Math.random() - 0.5) / 4,
          1.2 + (Math.random() - 0.5) / 100,
        )
      );
    }

    return result;
  }

  constructor(splits: SplitsData) {
    super();

    this.splits = splits;
  }

  protected crossover(a: CurveFit, b: CurveFit): CurveFit {
    const newVmax = (a.vmax + b.vmax) / 2 + (Math.random() - 0.5) / 8;
    const newTau = (a.tau + b.tau) / 2 + (Math.random() - 0.5) / 100;

    return new CurveFit(newVmax, newTau);
  }

  protected shouldEnd(
    input: CurveFit,
    score: number,
    generation: number
  ): boolean {
    if (generation >= MAX_GENERATIONS) {
      return true;
    }

    return score < OPTIMIZED_SCORE;
  }

  protected evaluate(input: CurveFit): number {
    return Math.abs(input.getSum(this.splits));
  }
}
