import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export type SplitsData = Record<number, number | undefined>;

const INITIAL_STATE = Object.freeze<SplitsData>({
    // 5: 1.54,
    // 10: 2.382,
    // 15: 3.105,
    // 20: 3.812,
    // 25: 4.494,
    // 30: 5.192,
});

export interface SplitData {
  split: number;
  value: number | undefined;
}

export const SPLITS_DATA_SLICE = createSlice({
  name: "splitsData",
  initialState: INITIAL_STATE,
  reducers: {
    setSplitData: (state: SplitsData, action: PayloadAction<SplitData>) => {
      const { split, value } = action.payload;

      state[split] = value;
    },
    removeSplit: (state: SplitsData, action: PayloadAction<number>) => {
      delete state[action.payload];
    },
  },
});
