import * as React from "react";
import { combineReducers, configureStore } from "@reduxjs/toolkit";
import memoizeOne from "memoize-one";
import { Provider } from "react-redux";
import { INPUT_DATA_SLICE } from "../features/input-data/InputDataSlice";
import { SPLITS_DATA_SLICE } from "../features/splits/SplitsSlice";
import { DATA_FORMAT_SLICE } from "../features/data-format/DataFormatSlice";

const getStore = memoizeOne(() => {
  const combinedReducers = combineReducers({
    dataFormat: DATA_FORMAT_SLICE.reducer,
    inputData: INPUT_DATA_SLICE.reducer,
    splits: SPLITS_DATA_SLICE.reducer,
  });

  return configureStore({
    reducer: combinedReducers,
  });
});

export function ReduxProvider(
  props: React.PropsWithChildren<object>
): JSX.Element {
  const store = getStore();

  return <Provider store={store}>{props.children}</Provider>;
}

export type RootState = ReturnType<ReturnType<typeof getStore>["getState"]>;
