export abstract class Optimizer<P> {
  protected abstract evaluate(input: P): number;
  protected abstract shouldEnd(
    input: P,
    score: number,
    generation: number
  ): boolean;
  protected abstract crossover(a: P, b: P): P;

  findOptimal(population: readonly P[]): P {
    let optimal: EvaluatedInput<P> = {
      input: population[0],
      score: Number.POSITIVE_INFINITY,
    };
    let generation = 0;

    while (!this.shouldEnd(optimal.input, optimal.score, generation++)) {
      let results = population.map((input) => ({
        input,
        score: this.evaluate(input),
      }));

      for (const current of results) {
        if (current.score < optimal.score) {
          optimal = current;
        }
      }

      population = this.baseCrossover(results);
    }

    return optimal.input;
  }

  private baseCrossover(previousGeneration: EvaluatedInput<P>[]): P[] {
    const nextGeneration = new Array<P>(previousGeneration.length);

    for (let i = 0; i < nextGeneration.length; i++) {
      let j = Math.floor(nextGeneration.length * Math.random()); // TODO ensure j != i

      let parentA = previousGeneration[i].input;
      let parentB = previousGeneration[j].input;

      let child = this.crossover(parentA, parentB);

      nextGeneration[i] = child;
    }

    return nextGeneration;
  }
}

export interface EvaluatedInput<P> {
  input: P;
  score: number;
}
