import * as React from "react";
import { TextField } from "@mui/material";
import { isFloat } from "../../common/utilities/isInteger";
import { useDataFormat } from "./DataFormatSelectors";
import { DistanceFormat } from "./DataFormatSlice";
import {
  convertFromDistanceFormat,
  convertToDistanceFormat,
} from "./convertToDataFormat";
import { NumericInput } from "../../common/components/NumericInput";

export interface DataFormatInputProps {
  label: string;
  value: number | undefined;
  onChange(newValue: number | undefined): void;
}

export function DataFormatInput(props: DataFormatInputProps): JSX.Element {
  const { value, label, onChange } = props;
  const { distanceFormat } = useDataFormat();

  const onValueChanged = (newValue: number | undefined) => {
    onChange(convertFromDistanceFormat(distanceFormat, newValue));
  };

  const unitValue = React.useMemo(
    () => convertToDistanceFormat(distanceFormat, value),
    [distanceFormat, value]
  );

  const unitFormat = distanceFormat === DistanceFormat.YARDS ? "(y)" : "(m)";

  return (
    <NumericInput
      label={`${label} ${unitFormat}`}
      value={unitValue}
      onChange={(e) => onValueChanged(e)}
    />
  );
}
