import * as React from 'react';
import {FormLabel} from '@mui/material';
import {useDataFormat} from './DataFormatSelectors';
import {convertToDistanceFormat} from './convertToDataFormat';

export interface DistanceFormatLabelProps{
    value: number | undefined;
}

export function DistanceFormatLabel(props: DistanceFormatLabelProps): JSX.Element{
    const {value} = props;
    const {distanceFormat} = useDataFormat();

    const convertedValue = React.useMemo(() => convertToDistanceFormat(distanceFormat, value), [distanceFormat, value]);

    return <FormLabel >{convertedValue?.toFixed(2)}</FormLabel>
}