import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
} from "@mui/material";
import { useFullDataSet } from "../../common/selectors/DataSelectors";
import { formatAsPercentage } from "../../common/utilities/formatAsPercentage";

export function DataSet(): JSX.Element | null {
  const velocityData = useFullDataSet();

  return (
    <Table>
      <TableHead>
        <TableRow>
          <TableCell>Time</TableCell>
          <TableCell>Position</TableCell>
          <TableCell>Velocity</TableCell>
          <TableCell>HZT acceleration (t)</TableCell>
          <TableCell>Air Friction (N)</TableCell>
          <TableCell>HZT Net Force (N)</TableCell>
          <TableCell>HZT Net Force (N/kg)</TableCell>
          <TableCell>HZT Power (W)</TableCell>
          <TableCell>HZT Power (W/kg)</TableCell>
          <TableCell>VTC Force</TableCell>
          <TableCell>Ratio of Force (%)</TableCell>
        </TableRow>
      </TableHead>
      <TableBody>
        {velocityData.map((intervalData) => (
          <TableRow key={intervalData.time}>
            <TableCell>{intervalData.time.toFixed(2)}</TableCell>
            <TableCell>{intervalData.position.toFixed(2)}</TableCell>
            <TableCell>{intervalData.velocity.toFixed(2)}</TableCell>
            <TableCell>{intervalData.hztAccel.toFixed(2)}</TableCell>
            <TableCell>{intervalData.airFriction.toFixed(2)}</TableCell>
            <TableCell>{intervalData.hztNetForce.toFixed(0)}</TableCell>
            <TableCell>
              {intervalData.hztNetForcePerWeight.toFixed(2)}
            </TableCell>
            <TableCell>{intervalData.hztNetPower.toFixed(0)}</TableCell>
            <TableCell>
              {intervalData.hztNetPowerPerWeight.toFixed(2)}
            </TableCell>
            <TableCell>
              {intervalData.vtcForce === undefined ? (
                <span>&mdash;</span>
              ) : (
                intervalData.vtcForce
              )}
            </TableCell>
            <TableCell>
              {intervalData.ratioOfForce === undefined ? (
                <span>&mdash;</span>
              ) : (
                formatAsPercentage(intervalData.ratioOfForce, 0)
              )}
            </TableCell>
          </TableRow>
        ))}
      </TableBody>
    </Table>
  );
}
